<template>
<div>
    <div class="row">
        <form class="form" @submit.prevent="createTask">
            <div class="col s6 offset-s3">
                <div class="input-field">
                    <label for="task">Task</label>
                    <input type="text" name="task" v-model="task" :class="[errors.task ? 'invalid' : 'validate']">
                    <span class="helper-text" data-error="Task must not be empty"></span>
                </div>
                <button type="submit" class="waves-effect waves-light btn">Add</button>
            </div>
        </form>
    </div>

    <p class="error" v-if="error">{{ error }}</p>
    <Task :tasks="tasks" v-on:delete-task="deleteTask" />
</div>
</template>

<script>
import TaskService from '../TaskService'
import Task from './Task'

export default {
    name: 'TaskComponent',
    components: {
        Task
    },
    data() {
        return {
            tasks: [],
            errors: {},
            error: '',
            task: ''
        }
    },
    methods: {
        async createTask() {
            if (!this.validForm()) {
                return
            }
            await TaskService.insertTask(this.task)
            this.tasks = await TaskService.getTask()
            this.task = ""
        },
        async deleteTask(id) {
            await TaskService.deleteTask(id)
            this.tasks = await TaskService.getTask()
        },
        validForm() {
            this.errors = {};
            if (this.task.trim() === "") {
                this.errors.task = "Task"
            }
            if (Object.keys(this.errors).length > 0) {
                return false;
            } else return true;
        }
    },
    async created() {
        try {
            this.tasks = await TaskService.getTask()
        } catch (err) {
            this.err = err.message
        }
    },
}
</script>

<style scoped>
.row {
    margin-top: 2rem;
}
</style>
