<template>
<div>
    <div class="row">
        <div class="col s6 offset-s3" v-for="(task, index) in tasks" :key="task._id" :item="task" :index="index" @dblclick="$emit('delete-task', [task._id])">
            <div class="card">
                <div class="card-content">
                    <span class="timestamp">
                        <p class="center-align">{{ task.dateCreate | formatDate }}</p>
                    </span>
                    <span class="card-title">
                        <p class="center-align">{{ task.task }}</p>
                    </span>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
    name: 'Task',
    props: ["tasks"],
    filters: {
        formatDate(date) {
            date = new Date(date);
            const day = date.getDate();
            const month = date.getMonth() + 1;
            const year = date.getFullYear();
            return `${day}-${month}-${year}`;
        }
    }
}
</script>

<style scoped>
.card {
    position: relative;
    padding: 10px 10px 30px 10px;
    margin-bottom: 15px;
    border-radius: 3px;
}

.timestamp {
    font-weight: normal;
}

.card-title {
    padding-top: 1.5rem;
    font-weight: 500;
}
</style>
